const { default: axios } = require("axios");
var FormData = require("form-data");
var Buffer = require("buffer/").Buffer;
var $ = require("jquery");

const returnImg = async (urlImg, sessionId, log) => {
  console.log("sessionId", sessionId);
  const dataImg = await axios.get(urlImg, {
    headers: {
      Cookie: `${sessionId}`,
    },
    responseType: "arraybuffer",
  });
  let base64ImageString = Buffer.from(dataImg.data, "binary").toString(
    "base64"
  );
  let srcValue = "data:image/png;base64," + base64ImageString;
  $("#img-captcha").attr("src", srcValue);
};

const getCodeCaptcha = async (keyApi, urlImg, sessionId, log) => {
  console.log("sessionId", sessionId);
  const dataImg = await axios.get(urlImg, {
    headers: {
      Cookie: `${sessionId}`,
    },
    responseType: "arraybuffer",
  });
  let base64ImageString = Buffer.from(dataImg.data, "binary").toString(
    "base64"
  );
  let srcValue = "data:image/png;base64," + base64ImageString;

  var formData = new FormData();
  formData.append("base64Image", srcValue);
  formData.append("language", "eng");
  formData.append("apikey", "bf52b8375888957");
  formData.append("isOverlayRequired", "false");
  formData.append("filetype", "jpg");

  try {
    const result = await $.ajax({
      url: "https://api.ocr.space/parse/image",
      data: formData,
      dataType: "json",
      cache: false,
      contentType: false,
      processData: false,
      type: "POST",
    });

    return String(result.ParsedResults[0]?.ParsedText).trim();
  } catch (error) {
    return "";
  }
};

const moduleGetCaptcha = async (keyApi, urlImg, sessionId, log) => {
  while (true) {
    try {
      const url = urlImg + new Date().getTime();
      const result = await getCodeCaptcha(keyApi, url, sessionId, log);
      if (result) {
        return result;
      }
    } catch (error) {
      log && console.log(error);
    }
  }
};

module.exports = {
  getImgCaptcha: returnImg,
  moduleGetCaptcha: moduleGetCaptcha,
};
