const { default: axios } = require("axios");
const encryptString = require("./encode.js");
const moduleImg = require("./getImg.js");

const temp = async (url, idCourse, NET_SessionId) => {
  try {
    const data = await axios.post(
      url,
      { termVal: idCourse },
      {
        headers: {
          Cookie: `${NET_SessionId}`,
        },
      }
    );
    console.log("---->success temp", data.data);
  } catch (error) {
    console.log("temp err->", error);
  }
};

const loadCaptcha = async (url, NET_SessionId) => {
  try {
    console.log("loadCaptcha", url);
    const data = await axios.get(url, {
      headers: {
        Cookie: `${NET_SessionId}`,
        Referer:
          "https://mydtu.duytan.edu.vn/sites/index.aspx?p=home_registeredall&semesterid=76&yearid=74",
      },
    });

    console.log("---->loadCaptcha oke");
  } catch (error) {
    console.log("loadCaptcha err->", error);
  }
};

const checkCaptcha = async (captcha, NET_SessionId) => {
  try {
    const data = await axios.post(
      "https://mydtu.duytan.edu.vn/Modules/regonline/ajax/LoadCaptcha.aspx/CheckCapt",
      {
        captValue: captcha,
      },
      {
        headers: {
          Cookie: `${NET_SessionId}`,
        },
      }
    );

    console.log("---->checkCaptcha oke", data.data);

    if (+data.data.d === 1) {
      return true;
    }

    return false;
  } catch (error) {
    console.log("checkCaptcha - err->", error);
    return false;
  }
};

const newCdate = () => {
  const tempdate = new Date();
  const cdate =
    tempdate.getDate() +
    "" +
    tempdate.getMonth() +
    "" +
    tempdate.getFullYear() +
    "" +
    tempdate.getHours() +
    "" +
    tempdate.getMinutes() +
    "" +
    tempdate.getSeconds();

  return cdate;
};

// step 1  -> temp -> loadCaptcha -> getImgCaptcha -> for user input captcha

// step 2  -> checkCaptcha  (true)->  para ->  encryptedPara ->  res
//                          (false)-> getImgCaptcha -> for user re input captcha

const urlTemp =
  "https://mydtu.duytan.edu.vn/Modules/regonline/ajax/LoadCaptcha.aspx/Temp";

const urlLoadCaptcha = () =>
  "https://mydtu.duytan.edu.vn/Modules/regonline/ajax/LoadCaptcha.aspx?cdate=" +
  newCdate();

const urlImgCaptcha =
  "https://mydtu.duytan.edu.vn/Modules/portal/JpegImage.aspx?";

const addClassProcessing =
  "https://mydtu.duytan.edu.vn/Modules/regonline/ajax/RegistrationProcessing.aspx/AddClassProcessing";

//-==========================================================
async function step1(idCourse, idStudent, NET_SessionId) {
  await temp(urlTemp, idCourse, NET_SessionId);

  await loadCaptcha(urlLoadCaptcha(), NET_SessionId);

  await moduleImg.getImgCaptcha(urlImgCaptcha, NET_SessionId);
}

//-==========================================================

async function step2(idCourse, idStudent, cookie, captcha, options) {
  const NET_SessionId = cookie;

  console.log(NET_SessionId, idCourse, idStudent,options);
  const { semesterid, yearid } = options;

  
  const checkCaptchaValue = await checkCaptcha(captcha, NET_SessionId);

  if (checkCaptchaValue) {
    const para = `${idCourse},${yearid},${semesterid},${idStudent},700,${captcha.trim()}`;
    //classRegCode +"," +year +"," +semester +"," +studentIdNumber +"," +curriculumId +"," +capt;
    //curriculumId = 700 or 614
    const encryptedPara = encryptString(para);

    const res = await axios.post(
      addClassProcessing,
      {
        encryptedPara: `${encryptedPara}`,
      },
      {
        headers: {
          Cookie: `${NET_SessionId}`,
        },
      }
    );

    return {
      status: true,
      d: res.data.d,
    };
  } else {
    await step1(idCourse, idStudent, cookie);
    return {
      status: false,
      d: "Mã xác nhận không đúng mời nhập lại",
    };
  }
}

module.exports = {
  step1,
  step2,
};
